export default "import { Field } from '@jafar/react-form';\n\nconst getGrid = (templateAreas) => {\n  const fieldIds = templateAreas.join(' ').split(' ').filter(x => x !== '.');\n  return {\n    templateAreas,\n    elements: fieldIds.map(id => ({ \n      selector: `#${id}`, \n      gridArea: id, \n      component: Field, \n      props: { id },\n      style: 'width: 350px;',\n    })),\n  };\n};\n\nexport default [{\n  id: 'personal-information',\n  title: 'Personal Information',\n  grid: getGrid([\n    'firstName lastName .',\n    'personalId address .',\n  ]),\n}, {\n  id: 'job-information',\n  title: 'Job Information',\n  grid: getGrid([\n    'department level .',\n    'benefits . .',\n  ]),\n}, {\n  id: 'raw-data',\n  title: 'Raw Data',\n  grid: getGrid([\n    'id creationDate .',\n    'modifier modificationDate .',\n  ]),\n  sections: [{\n    id: 'raw-data-general',\n    title: 'General',\n    grid: getGrid([\n      'id modifier .',\n    ]),\n  }, {\n    id: 'raw-data-modification',\n    title: 'Modification',\n    grid: getGrid([\n      'creationDate modificationDate .',\n    ]),\n  }],\n}];\n";